html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #fff;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #ea4c89;
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  color: #fff;
}
.heading-blanco span {
  color: #fff;
}
.heading-blanco::after {
  display: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.contenidoGrid {
  display: flex;
  position: relative;
}

.sidebar {
  display: none;
}
@media (min-width: 960px) {
  .sidebar {
    display: block;
    min-width: 52.4rem;
    max-width: 52.4rem;
    background-color: #F1CDD7;
    height: 100vh;
  }
}
.sidebar__head {
  padding: 3rem 0 3rem 6rem;
}
.sidebar__heading {
  font-family: "Rochester", cursive;
  text-transform: lowercase;
  font-size: 3rem;
  text-align: left;
  color: #865C6C;
}
@media (min-width: 1400px) {
  .sidebar__heading {
    font-size: 4rem;
  }
}
.sidebar__descripcion {
  font-size: 2.8rem;
  font-weight: 700;
  color: #865C6C;
  line-height: 1;
  max-width: 40rem;
}
@media (min-width: 1400px) {
  .sidebar__descripcion {
    font-size: 3.4rem;
  }
}
.sidebar__imagen {
  margin: 5rem 0;
  width: 100%;
  display: none;
}
@media (min-width: 1400px) {
  .sidebar__imagen {
    display: block;
  }
}
.sidebar__by {
  display: none;
}
@media (min-width: 1400px) {
  .sidebar__by {
    display: block;
    color: #865C6C;
    font-size: 1.6rem;
    padding-left: 4rem;
  }
}
.sidebar__autor {
  text-decoration: underline;
}

.login {
  padding: 2rem;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .login {
    margin-top: 12rem;
  }
}
@media (min-width: 1400px) {
  .login {
    margin-top: 28rem;
  }
}
.login__memberDesktop {
  display: none;
}
@media (min-width: 960px) {
  .login__memberDesktop {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    position: absolute;
    top: 0;
    right: 0;
    padding: 2.2rem 2.6rem 0 0;
  }
}
.login__notMember {
  font-size: 1.4rem;
}
.login__singUp {
  color: #1877f2;
  font-size: 1.4rem;
}
.login__memberMovil {
  margin: 2rem 0;
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: center;
}
@media (min-width: 960px) {
  .login__memberMovil {
    display: none;
  }
}

.inicio {
  max-width: 40rem;
  margin: 0 auto;
}
.inicio__heading {
  text-align: left;
  font-family: "Rochester", cursive;
  font-size: 3.8rem;
  text-transform: lowercase;
  margin: 0;
}
@media (min-width: 960px) {
  .inicio__heading {
    display: none;
  }
}
.inicio__singIn {
  font-weight: 700;
  padding: 1rem 0;
  font-size: 2.8rem;
}

.enlaces {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  column-gap: 2rem;
}
.enlaces__accederGoogle {
  background-color: #1877f2;
  padding: 0.2rem;
  border-radius: 0.5rem;
  color: #fff;
  font-size: 1.4rem;
  font-weight: 500;
  display: flex;
  justify-content: flex-start;
  column-gap: 3rem;
  align-items: center;
  min-width: 25rem;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.enlaces__accederGoogle:hover {
  background-color: #4893f5;
}
.enlaces__imgGoogle {
  background-color: #fff;
  border-radius: 0.2rem;
  padding: 0.7rem;
}
.enlaces__imagenGoogle {
  width: 1.8rem;
}
.enlaces__accederTwitter {
  background-color: #e9ecef;
  color: #5b6d7e;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  transition-property: background-color color;
  transition-duration: 0.3s;
}
.enlaces__accederTwitter:hover {
  background-color: #dadfe4;
  color: #1877f2;
}
.enlaces__divider {
  margin: 4rem 0;
  color: #e9ecef;
}

.form__label {
  font-weight: 900;
}
.form__input {
  background-color: #e9ecef;
  border: 1px solid transparent;
  width: 100%;
  padding: 1rem;
  border-radius: 1rem;
  margin: 1rem 0 2.5rem 0;
  transition-property: background-color border box-shadow;
  transition-duration: 0.3s;
}
.form__input:hover {
  background-color: #fff;
  border: 1px solid #e9ecef;
  box-shadow: rgba(234, 76, 137, 0.1) 0px 0px 5px 4px;
}
.form__input:focus {
  outline: none;
  background-color: #fff;
  border: 1px solid #e9ecef;
  box-shadow: rgba(234, 76, 137, 0.1) 0px 0px 5px 4px;
}
.form__pass {
  display: flex;
  justify-content: space-between;
}
.form__submit {
  width: 100%;
  padding: 1rem;
  border: 1px solid transparent;
  border-radius: 1rem;
  background-color: #ea4c89;
  color: #fff;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 960px) {
  .form__submit {
    width: 50%;
  }
}
.form__submit:hover {
  background-color: #ef7aa7;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #ea4c89;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #fff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}